import {Routes} from '@angular/router';

import {InstallationAddComponent}  from './installation.add/installation.add.component';
import {InstallationListComponent} from "./installation.list/installation.list.component";
import {InstallationEditComponent} from "./installation.edit/installation.edit.component";

export const InstallationRoutes:Routes = [{
  path: '',
  redirectTo: 'list',
  pathMatch: 'full',
}, {
  path: '',
  children: [{
    path: 'add', component: InstallationAddComponent
  },
    {path: 'list', component: InstallationListComponent},
    {path: 'edit/:id', component: InstallationEditComponent},

  ]
}];



