import {NgModule} from '@angular/core';
import {RouterModule} from '@angular/router';
import {CommonModule} from '@angular/common';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {FileUploadModule} from 'ng2-file-upload/ng2-file-upload';
import {WizardModule} from 'ng2-archwizard';
import {InstallationRoutes} from './installation.routing';
import {InstallationService} from "./service/installation.service";
import {InstallationAddComponent} from './installation.add/installation.add.component';
import {InstallationListComponent} from "./installation.list/installation.list.component";
import {InstallationEditComponent} from "./installation.edit/installation.edit.component";
import {DataTableModule} from "angular2-datatable";
import {ClientService} from "../client/service/client.service";

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    FileUploadModule,
    DataTableModule,
    WizardModule,
    RouterModule.forChild(InstallationRoutes)
  ],
  declarations: [
    InstallationAddComponent,
    InstallationListComponent,
    InstallationEditComponent,
  ],
  providers: [
    InstallationService,
    ClientService,
  ],
})

export class InstallationModule {
}



