"use strict";

import { Component, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { fadeInAnimation } from "../../core/route-animation/route.animation";
import { Installation } from "../metier/installation.metier";
import { FormGroup, FormBuilder } from "@angular/forms";
import { BaseComponent } from "../../core/component/base-component";
import { InstallationService } from "../service/installation.service";
import { PageTitleService } from "../../core/page-title/page-title.service";
import { Client } from "../../client/metier/client.metier";
import { ClientService } from "../../client/service/client.service";

@Component({
  selector: 'ms-installation-list',
  templateUrl: './installation.list.component.html',
  styleUrls: ['./installation.list.component.scss'],
  encapsulation: ViewEncapsulation.None,
  host: {
    "[@fadeInAnimation]": 'true'
  },
  animations: [fadeInAnimation]

})
export class InstallationListComponent extends BaseComponent implements OnInit {

  public installationForm: FormGroup;
  private installation: Installation;
  private installationList: Array<Installation> = [];
  private clientList: Array<Client> = [];

  constructor(private fb: FormBuilder,
    private installationService: InstallationService,
    private clientService: ClientService,
    private pageTitleService: PageTitleService) {
    super();
    this.createForm();
  }

  ngOnInit() {
    this.pageTitleService.setTitle("LISTE DES NOUVELLES INSTALLATIONS");
    this.loadInstallationList();
    this.loadClientList();
  }

  createForm() {
    this.installationForm = this.fb.group({
      client: null,
    });
  }


  findInstallationList(installationForm: any) {
    this.installation = new Installation(installationForm);
    this.installationService.findInstallationListByCriteria(this.installation)
      .subscribe(installationList => {
        this.installationList = installationList;
      }, error => this.showError(error.status, error.json().message))
      ;
  }

  private loadInstallationList() {
    this.installationService.getInstallationList()
      .subscribe(installationList => {
        this.installationList = installationList;
      }, error => this.showError(error.status, error.json().message)
      )
      ;
  }

  private loadClientList() {
    this.clientService.getClientList()
      .subscribe(clientList => {
        this.clientList = clientList;
      }, error => this.showError(error.status, error.json().message)
      )
      ;
  }

  chargeInstallationRedirect(installation_id) {
    this.router.navigate(['/charge_installation/list'], { queryParams: { installation_id: installation_id } });
  }

}



