import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {PageTitleService} from '../../core/page-title/page-title.service';
import {fadeInAnimation} from "../../core/route-animation/route.animation";
import {Installation} from "../metier/installation.metier";
import {InstallationService} from "../service/installation.service";
import {BaseComponent} from "../../core/component/base-component";
import {Client} from "../../client/metier/client.metier";
import {ClientService} from "../../client/service/client.service";
import {CustomValidators} from "ng2-validation";

@Component({
  selector: 'ms-installation-add',
  templateUrl: './installation.add.component.html',
  styleUrls: ['./installation.add.component.scss'],
  encapsulation: ViewEncapsulation.None,
  host: {
    "[@fadeInAnimation]": 'true'
  },
  animations: [fadeInAnimation]
})
export class InstallationAddComponent extends BaseComponent implements OnInit {

  public installationForm: FormGroup;
  private installation: Installation;
  private clientList: Array<Client> = [];

  constructor(private fb: FormBuilder, private pageTitleService: PageTitleService,
              private installationService: InstallationService) {
    super();
  }

  ngOnInit() {
    this.pageTitleService.setTitle("AJOUTER UNE NOUVELLE INSTALLATION");
    this.installationForm = this.fb.group({
      date: [new Date().toISOString().substring(0, 10), Validators.compose([Validators.required, CustomValidators.date])],
      client: ['', Validators.required],
    });

    this.loadClientList();
  }

  saveInstallation(installationForm: any) {
    this.installation = new Installation(installationForm);
    this.installationService.saveInstallation(this.installation)
      .subscribe(() => {
        this.showInfo("Enregistrement effectué avec succès");
        this.reset();
        this.router.navigate(["/installation/list"]);
      }, error => this.showError(error.status, error.json().message))
    ;
  }


  private loadClientList() {
    this.installationService.getClientList()
      .subscribe(clientList => {
          this.clientList = clientList;
        }, error => this.showError(error.status, error.json().message)
      )
    ;
  }

  reset() {
    this.installationForm.reset();
  }

}






