import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { PageTitleService } from '../../core/page-title/page-title.service';
import { fadeInAnimation } from "../../core/route-animation/route.animation";
import { Installation } from "../metier/installation.metier";
import { InstallationService } from "../service/installation.service";
import { BaseComponent } from "../../core/component/base-component";
import { Params, ActivatedRoute } from "@angular/router";
import { Client } from "../../client/metier/client.metier";
import { ClientService } from "../../client/service/client.service";
import { CustomValidators } from "ng2-validation";


@Component({
  selector: 'ms-installation-edit',
  templateUrl: './installation.edit.component.html',
  styleUrls: ['./installation.edit.component.scss'],
  encapsulation: ViewEncapsulation.None,
  host: {
    "[@fadeInAnimation]": 'true'
  },
  animations: [fadeInAnimation]
})
export class InstallationEditComponent extends BaseComponent implements OnInit {

  private installation: Installation;
  public installationForm: FormGroup;
  private clientList: Array<Client> = [];


  constructor(private fb: FormBuilder,
    private route: ActivatedRoute,
    private pageTitleService: PageTitleService,
    private installationService: InstallationService,
    private clientService: ClientService) {
    super();
  }

  ngOnInit() {
    this.pageTitleService.setTitle("MODIFIER UNE INSTALLATION");
    this.loadInstallation();
    this.installationForm = this.fb.group({
      id: [Validators.required],
      date: [new Date().toISOString().substring(0, 10), Validators.compose([Validators.required, CustomValidators.date])],
      client: ['', Validators.required],
    });
  }

  updateInstallation(installationForm: any) {
    this.installation = new Installation(installationForm);
    this.installationService.saveInstallation(this.installation)
      .subscribe(() => {
        this.showInfo("Enregistrement effectué avec succès");
        this.router.navigate(['/installation/list']); // Redierction vers list
      }, error => this.showError(error.status, error.json().message))
      ;
  }

  loadInstallation() {
    this.route.params.switchMap((params: Params) => this.installationService.getInstallation(+params['id']))
      .subscribe(installation => {
        this.installation = installation;
        this.installationForm.patchValue(this.installation);
        this.loadClientList();

        if (this.installation.facturee === true) {
          this.installationForm.get('date').disable();
          this.installationForm.get('client').disable();
        }


      }, error => this.showError(error.status, error.json().message)
      );
  }

  deleteInstallation(installationForm: any) {
    if (!confirm("Etes-vous de vouloir supprimer ces enregistrements !")) {
      return;
    }
    this.installation = new Installation(installationForm);
    this.installationService.deleteInstallation(this.installation)
      .subscribe(() => {
        this.showInfo("Suppression effectué avec succès");
        this.router.navigate(['/installation/list']); // Redierction vers list
      }, error => this.showError(error.status, error.json().message))
      ;
  }

  private loadClientList() {
    this.clientService.getClientList()
      .subscribe(clientList => {
        this.clientList = clientList;
        this.installation.client = this.clientList.find(client => client.id === this.installation.client.id);
        this.installationForm.patchValue({ client: this.installation.client }, { onlySelf: true });
      }, error => this.showError(error.status, error.json().message)
      )
      ;
  }

  chiffrageRedirect() {
    this.router.navigate(['/chiffrage/list'], { queryParams: { installation_id: this.installation.id } });
  }

  private isDisabled() {
    if (this.installation != null) {
      if (this.installation.facturee === true) {
        return true;
      } else {
        return false;
      }
    }
  }

}






